import Swiper, { Navigation, Autoplay } from 'swiper';

Swiper.use([Navigation, Autoplay]);

const carousel = document.querySelector('.swiper-container');

let mySwiper;

function mobileCarousel() {
	if (window.innerWidth <= 768 && carousel.dataset.mobile === 'false') {
		// eslint-disable-next-line
		mySwiper = new Swiper(carousel, {
			slidesPerView: 1,
			// spaceBetween: 10,
			loop: true,
			slideClass: 'swiper-slide',
			navigation: {
				nextEl: '.swiper-button-next',
				prevEl: '.swiper-button-prev',
			},
			autoplay: {
				delay: 2000,
				disableOnInteraction: false,
			},
		});

		carousel.dataset.mobile = 'true';
	}

	if (window.innerWidth > 768) {
		carousel.dataset.mobile = 'false';
		if (carousel.classList.contains('swiper-container-initialized')) {
			mySwiper.destroy();
		}
	}
}

mobileCarousel();

window.addEventListener('resize', () => {
	mobileCarousel();
});
